(function() {
  'use strict';
  
  angular.module('admin.admin_users')
  .controller('adminUserUpdateController', adminUserUpdateController)
  .config(function($stateProvider) {
    $stateProvider
      .state('admin.admin_users.update', {
        url: '/update/:_id',
        title: 'Editar Usuario',
        templateUrl: 'app/modules/users/admin/views/admin.users.update.html',
        controller: 'adminUserUpdateController',
        controllerAs: 'adminUserCtrl',
        resolve: {
          initial_data: function(admin_users_model, $stateParams) {
            return admin_users_model.get_data_for_update_admin_user($stateParams._id);
          }
        }
      });
  });
  
  function adminUserUpdateController($scope, $state, initial_data, admin_users_model, store_users_model, Notifier, _) {
    var vm = this;
    
    vm.user = initial_data.user;
    vm.profiles = initial_data.profiles;
    vm.shops = initial_data.shops;
    vm.update_password = false;
    vm.change_password = false;
    vm.controller = 'update';
    
    $scope.$watch('adminUserCtrl.user.profile', function() {
      vm.display_extra_fields = false;
      
      var commercial_user = _(vm.profiles).findWhere({_id: vm.user.profile});
      if (commercial_user && commercial_user.role == 2) {
        vm.display_extra_fields = true;
        vm.user.role = 2;
      }
      else {
        vm.user.role = 1;
        vm.user.shop = null;
      }
    });
    
    vm.change_password = function() {
      vm.update_password = !vm.update_password;
    }
    
    vm.update = function() {
      var current_model = vm.display_extra_fields? store_users_model : admin_users_model;
      
      current_model.update_admin_user(vm.user).then(function() {
        Notifier.success({custom_message: 'Usuario ' + vm.user.name + ' '+ vm.user.surname + ' Actualizado.'});
        $state.go('admin.admin_users.list');
      });
    };    

  }
})();
(function() {
  'use strict';
  
  angular.module('admin.admin_users')
  .controller('adminUserController', adminUserController)
  .config(function($stateProvider) {
    $stateProvider
      .state('admin.admin_users.list', {
        url: '/list',
        title: 'Lista Usuarios',
        templateUrl: 'app/modules/users/admin/views/admin.users.list.html',
        controller: 'adminUserController',
        controllerAs: 'adminUserCtrl',
        resolve: {
          users: function(admin_users_model) {
            return admin_users_model.get();
          }
        }
      });
  });
  
  function adminUserController($state, admin_users_model, users, NgTableParams, _) {
    var vm = this;    
    
    vm.users = users;
    
    parse_users();
    
    function parse_users() {
      _(vm.users).each(function(user) {
        user.state = 'Activo';
        
        if (!user.enabled) {
          user.state = 'Inactivo';
        }
      });
    }
    
    set_admin_users_pagination();
    
    function set_admin_users_pagination() {     
      vm.tableParams = new NgTableParams({
      }, {
        dataset: vm.users
      });
    }
    
    vm.edit_user = function(user) {
      $state.go('admin.admin_users.update', { _id: user._id });
    };
    
    vm.remove_user = function(user) {
      vm.close_remove_user_modal = false;
      vm.user = user;
    };
    
    vm.delete_user = function() {      
      vm.close_remove_user_modal = true;
      vm.user.enabled = !vm.user.enabled;
      
      admin_users_model.update(vm.user).then(function() {
        parse_users();
      });
    };  
  }
})();
(function() {
  'use strict';
  
  angular
  .module('admin.admin_users')
  .controller('usersCreateController', usersCreateController)
  .config(function($stateProvider) {
    $stateProvider
      .state('admin.admin_users.create', {
        url: '/create',
        title: 'Nuevo Administrador',
        templateUrl: 'app/modules/users/admin/views/admin.users.create.html',
        controller: 'usersCreateController',
        controllerAs: 'adminUserCtrl',
        resolve: {
          initial_data: function(admin_users_model) {
            return admin_users_model.get_data_for_create_admin_user();
          }
        }      
      });
  });
  
  function usersCreateController($scope, $state, admin_users_model, store_users_model, Notifier, initial_data, _) {
    var vm = this;
    
    vm.profiles = initial_data.profiles;
    vm.shops = initial_data.shops;
    vm.update_password = true;
    vm.controller = 'create';
    
    $scope.$watch('adminUserCtrl.user.profile', function() {
      vm.display_extra_fields = false;
      
      if (vm.user && vm.user.profile) {
        var commercial_user = _(vm.profiles).findWhere({_id: vm.user.profile});
        if (commercial_user && commercial_user.role == 2) {
          vm.display_extra_fields = true;
        }
      }
    });
    
    vm.save = function() {
      var current_model = vm.display_extra_fields? store_users_model : admin_users_model;
      
      current_model.create(vm.user).then(function() {
        Notifier.success({custom_message: 'Usuario ' + vm.user.name + ' ' + vm.user.surname + ' creado.'});
        $state.go('admin.admin_users.list');
      });
    };
  }  
})();
