(function() {
  'use strict';

  angular
    .module('admin.layout')
    .directive('sideNavigation', sideNavigation);

  /** @ngInject */
  function sideNavigation($timeout) {
    return {
      restrict: 'A',
      link: function(scope, element) {
        // Call the metsiMenu plugin and plug it to sidebar navigation
        $timeout(function(){
          element.metisMenu();
        });
        
        // Colapse menu in mobile mode after click on element
        var menuElement = angular.element('#side-menu a:not([href$="\\#"])');
        menuElement.click(function(){
          if (angular.element(window).width() < 769) {
            angular.element("body").toggleClass("mini-navbar");
          }
        });
        
        // Enable initial fixed sidebar
        if (angular.element("body").hasClass('fixed-sidebar')) {
          var sidebar = element.parent();
          sidebar.slimScroll({
            height: '100%',
            railOpacity: 0.9
          });
        }
      }
    };
  }
})();

(function() {
  'use strict';
  
  angular
    .module('admin.layout')
    .directive('minimalizaSidebar', minimalizaSidebar);
    
    function minimalizaSidebar($timeout) {
      return {
        restrict: 'A',
        template: '<a class="navbar-minimalize minimalize-styl-2 btn btn-primary " href="" ng-click="minimalize()"><i class="fa fa-bars"></i></a>',
        controller: function ($scope) {
          
          $scope.minimalize = function () {
            
            angular.element("body").toggleClass("mini-navbar");
            
            if (!angular.element('body').hasClass('mini-navbar') || angular.element('body').hasClass('body-small')) {
              // Hide menu in order to smoothly turn on when maximize menu
              angular.element('#side-menu').hide();
              // For smoothly turn on menu
              $timeout(function () {
                angular.element('#side-menu').fadeIn(400);
              }, 200);
              
            }
            else if (angular.element('body').hasClass('fixed-sidebar')) {
              angular.element('#side-menu').hide();
              
              $timeout(function () {
                angular.element('#side-menu').fadeIn(400);
              }, 100);
            }
            else {
              // Remove all inline style from jquery fadeIn function to reset menu state
              angular.element('#side-menu').removeAttr('style');
            }
          };
        }
      };
    }

})();
(function() {
  'use strict';

  angular
    .module('admin.layout')
    .directive('iboxTools', iboxTools);

  /** @ngInject */
  function iboxTools($timeout) {
    return {
      restrict: 'A',
      scope: true,
      templateUrl: 'app/modules/main/views/ibox.tools.full.screen.html',
      controller: function ($scope, $element) {
        // Function for collapse ibox
        $scope.showhide = function () {
          var ibox = $element.closest('div.ibox');
          var icon = $element.find('i:first');
          var content = ibox.children('.ibox-content');
          content.slideToggle(200);
          // Toggle icon from up to down
          icon.toggleClass('fa-chevron-up').toggleClass('fa-chevron-down');
          ibox.toggleClass('').toggleClass('border-bottom');
          $timeout(function () {
            ibox.resize();
            ibox.find('[id^=map-]').resize();
          }, 50);
        };
        // Function for close ibox
        $scope.closebox = function () {
          var ibox = $element.closest('div.ibox');
          ibox.remove();
        };
        // Function for full screen
        $scope.fullscreen = function () {
          var ibox = $element.closest('div.ibox');
          var button = $element.find('i.fa-expand');
          angular.element('body').toggleClass('fullscreen-ibox-mode');
          button.toggleClass('fa-expand').toggleClass('fa-compress');
          ibox.toggleClass('fullscreen');
          $timeout(function() {
            angular.element(window).trigger('resize');
          }, 100);
        };
      }
    };
  }
})();
