(function () {
  'use strict';
  
  angular.module('core.utils')
  .factory('validator_utils', function(_) {
  
    function validate_luhn(id_number) {
      var even = false;
      var sum = 0;
      
      _.each(id_number.split('').reverse(), function(digit) {
        digit = parseInt(digit, 10);
        if (even) {
          digit = digit * 2;
        }
    
        sum += _.reduce(digit.toString().split(''), function(total, digit) {
          digit = parseInt(digit, 10);
          return total + digit;
        }, 0);
        even = !even;
      });
    
      if((sum % 10 === 0) && (sum !== 0)){
        return true;
      }
    
      return false;
    }
    
    function is_id_card(value) {
      
      var province = parseInt(value.substring(0, 2), 10);
      var invalid_province = province < 1 || province > 24;
  
      var third_digit = parseInt(value.substring(2, 3), 10);
      var invalid_third_digit = third_digit > 5;
  
      var invalid_length = value.length !== 10;
  
      var has_non_digit_character = /[^0-9]/.test(value);
  
      var invalid_luhn_verification = !validate_luhn(value);
      
      if (invalid_province ||
          invalid_third_digit ||
          invalid_length ||
          has_non_digit_character ||
          invalid_luhn_verification) {
        return false;
      }
      return true;
    }
    
    function is_legal_ruc(value) {
      
      var third_digit = parseInt(value.substring(2, 3), 10);
      var last_three_numbers = value.substring(10, 13);
      
      var invalid_length = value.length !== 13;
      var invalid_third_digit = (third_digit != 6 && third_digit != 9);
      var invalid_last_three_numbers = last_three_numbers != '001'
      
      if (invalid_length ||
          invalid_third_digit ||
          invalid_last_three_numbers) {
        return false;
      }
      return true;
    }
    
    function is_valid_password(value) {
      
      // at least one number, one lowercase and one uppercase letter
      // at least six characters that are letters, numbers or the underscore
      var re = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])\w{6,}$/;
      return re.test(value);
    }
    
    return {
      is_id_card: function(value) {
        return is_id_card(value);
      },
      is_legal_ruc: function(value) {
        return is_legal_ruc(value);
      },
      is_valid_password: function(value) {
        return is_valid_password(value);
      }
    };
  });
})();
/* global Big */
'use strict';

angular.module('core.utils').factory('math_utils', function(_) {

  // System decimal places
  var default_decimal_places = 2;

  function any_argument_null(args){
    return _.contains(args, null);
  }
  
  return {
    add: function() {
      var args = _.toArray(arguments);
      if (any_argument_null(args) || args.length === 0) { return null; }

      function cback(accum, curr) { return accum.plus(curr); }
      
      return Number(_.reduce(args, cback, new Big(0)));
    },

    subtract: function() {
      var args = _.toArray(arguments);
      if (any_argument_null(args) || args.length === 0) { return null; }

      var first = args[0];
      //delete args[0];
      args.shift();

      function cback(accum, curr) { return accum.minus(curr); }
      
      return Number(_.reduce(args, cback, new Big(first)));
    },

    multiply: function() {
      var args = _.toArray(arguments);
      if(any_argument_null(args) || args.length === 0){ return null; }

      function cback(accum, curr) { return accum.times(curr); }

      return Number(_.reduce(args, cback, new Big(1)));
    },

    divide: function() {
      var args = _.toArray(arguments);
      if(any_argument_null(args) || args.length === 0){ return null; }

      function cback(accum, curr) { return Big(accum).div(curr); }
      
      return Number(_.reduce(args, cback));
    },
    
    percentage: function() {
      
      var that = this;
      
      var args = _.toArray(arguments);
      if(any_argument_null(args) || args.length === 0){ return null; }
      
      function cback(accum, curr) {
        
        if (accum < curr) {
          return 0;
        }
        
        return that.divide(that.multiply(curr, 100), accum);
      }
      
      return Number(_.reduce(args, cback));
    },

    average: function(collection) {
      var that = this;
      if(any_argument_null(collection) || collection.length === 0) {
        return null;
      }
      var sum = _(collection).reduce(function(acum, num) { return that.add(acum, num); }, 0);
      
      return this.divide(sum, _.size(collection));
    },

    weighted_average: function(collection) {
      var that = this;
      
      if (collection.length === 0) { return null;}
      
      var added_weights = _.reduce(collection, function(acum, obj) { return that.add(acum, obj.weight); }, 0);

      return _.reduce(collection, function(average, obj) {
        return that.add(average, that.divide(that.multiply(obj.value, obj.weight), added_weights));
      }, 0);
    },
    
    truncated_average: function(collection) {
      return this.truncate(this.average(collection));
    },
    
    truncated_weighted_average: function(collection) {
      return this.truncate(this.weighted_average(collection));
    },

    truncate: function(num) {
      if (num || num===0) {
        return Number(Big(num).round(default_decimal_places, 0));
      }
      return null;
    },
    
    round: function(number, decimal_places) {
      var new_decimal_places = decimal_places || default_decimal_places;
      if (number || number === 0) {
        return Number(Big(number).round(new_decimal_places));
      }
      return null;
    }
  };
});

(function() {
  'use strict';
  
  angular.module('core.utils').factory('date_utils', function(moment) {
    
    function format_date(date, format) {
      format = format || 'YYYY-MM-DD';
      return date.format(format);
    }
    
    function initial_report_date(format) {
      
      var start_date = moment().add('months', -1);
      var end_date = moment();
      
      return {start_date: format_date(start_date, format), end_date: format_date(end_date, format)};
    }
    
    function date_diff_days(date1, date2) {
      return moment(date1).diff(moment(date2), 'days');
    }
    
    function date_diff_milliseconds(date1, date2) {
      return moment.duration(moment(date1).diff(moment(date2))).asMilliseconds();
    }
    
    function add_days(date, days) {
      var new_date = moment(date).add(days, 'days');
      return new_date;
    }
    
    return {
      current_date: function(format) {
        return format_date(moment(), format);
      },
      format_date: function(date, format) {
        return format_date(moment(date), format);
      },
      initial_report_date: function(format) {
        return initial_report_date(format);
      },
      date_diff_days: function(date1, date2) {
        return date_diff_days(date1, date2);
      },
      date_diff_milliseconds: function(date1, date2) {
        return date_diff_milliseconds(date1, date2);
      },
      add_days: function(date1, days) {
        return add_days(date1, days);
      }
    };
  });
})();
