(function() {
  
  'use strict';
  
  angular.module('core.services').factory('permissions_management', function(_) {
    return {
      build_permissions: function(modules, permissions) {
        var custom_permissions = {permission_names: []};
        //var keys_modules = Object.keys(modules);
        var permission_names = [];
        var action;
        
        _.each(modules, function(module) {
          
          permission_names = [];
          
          _.each(permissions, function(permission) {
            
            if (module.id === permission.moduleId) {
              permission_names.push(permission.action);
            }
          });
          
          var module_permissions = {
            find: [],
            create: [],
            update: [],
            remove: [],
            permission_names: permission_names
          };
          
          _.each(permission_names, function(permission_name) {
            
            custom_permissions.permission_names.push(permission_name);
            action = permission_name.split('_')[1];
            
            switch(action) {
              case 'find':
                module_permissions.find.push('all');
                break;
              case 'create':
                module_permissions.create.push('all');
                break;
              case 'update':
                module_permissions.update.push('all');
                break;
              case 'delete':
                module_permissions.remove.push('all');
                break;
            }
          });
          
          custom_permissions[module.name] = module_permissions;
        });
        
        return custom_permissions;
      }
    };
  });
})();
(function() {
  
  'use strict';
  
  angular.module('core.services').factory('access_routes', function(session, _) {
    
    var permissions, permission, splited_state, module, action;
    
    var method_action = {
      list: 'find',
      update: 'update',
      create: 'create',
      remove: 'remove'
    };
    
    var states_without_validation = [
      'login',
      'forgot_password',
      'admin.dashboard',
      'not_found',
      'admin.internal_error'
    ];
    
    function determine_string_permissions(state) {
      
      if (_.contains(states_without_validation, state)) {
        return true;
      }
      
      permissions = session.permission_names();
      permission = get_permission_name(state);
      
      return _.contains(permissions, permission);
    }
    
    function get_permission_name(state) {
      splited_state = state.split('.');
      module = splited_state[0];
      action = splited_state[1];
      
      if (splited_state[0] === 'admin') {
        module = splited_state[1];
        action = splited_state[2];
      }
      
      if (method_action[action]) {
        return module + "_can_" + method_action[action];
      }
      
      return module + "_can_" + action;
    }
    
    return {
      has_access: function(state) {
        return determine_string_permissions(state);
      }
    };
  });
})();
