(function () {
  'use strict';
  
  angular.module('core.directives')
  .directive('integer', function() {
    return {
      require: 'ngModel',
      link: function(scope, element, attrs, modelCtrl) {
        modelCtrl.$parsers.push(function (inputValue) {
          // this next if is necessary for when using ng-required on your input.
          // In such cases, when a letter is typed first, this parser will be called
          // again, and the 2nd time, the value will be undefined
          if (inputValue == undefined) {
            return '';
          }
          
          var transformedInput = inputValue.replace(/[^0-9]/g, '');
          
          if (transformedInput!=inputValue) {
            modelCtrl.$setViewValue(transformedInput);
            modelCtrl.$render();
          }
          
          return transformedInput;
        });
      }
    };
  });
})();
(function () {
  'use strict';
  
  angular.module('core.directives')
  .directive('idCard', function(validator_utils) {
  
    return {
      restrict: 'A',
      require: 'ngModel',
      priority: 0,
      link: function(scope, element, attrs, input_model) {
  
        scope.$watch('$parent.' + attrs.isIdentityCard, function() {
          validate_id_card();
        });
  
        scope.$watch(attrs.ngModel, function(new_value, old_value) {
          if (new_value !== old_value) {
            validate_id_card();
          }
        });
  
        function validate_id_card() {
          var error_key = 'invalid_id_card';
          var is_valid = true;
  
          input_model.$setValidity(error_key, is_valid);
  
          if (scope.$eval('$parent.' + attrs.isIdentityCard)) {
            var value = element.val();
  
            if (/\S/.test(value)) {
              is_valid = validator_utils.is_id_card(value);
              input_model.$setValidity(error_key, is_valid);
            }
          }
  
          if (!scope.$$phase) {
            scope.$digest();
          }
        }
      }
    };
  });
})();
(function () {
  'use strict';
  
  angular.module('core.directives')
  .directive('icheck', function ($timeout) {
    return {
      require: 'ngModel',
      link: function ($scope, element, $attrs, ngModel) {
        return $timeout(function () {
          var value;
          value = $attrs['value'];
          
          $scope.$watch($attrs['ngModel'], function () {
            angular.element(element).iCheck('update');
          });
          
          $scope.$watch($attrs['ngDisabled'], function (newValue) {
            angular.element(element).iCheck(newValue ? 'disable' : 'enable');
            angular.element(element).iCheck('update');
          })
          
          return angular.element(element).iCheck({
            checkboxClass: 'icheckbox_square-green',
            radioClass: 'iradio_square-green'
          })
          .on('ifChanged', function (event) {
            if (angular.element(element).attr('type') === 'checkbox' && $attrs['ngModel']) {
              $scope.$apply(function () {
                return ngModel.$setViewValue(event.target.checked);
              })
            }
          })
          .on('ifClicked', function () {
            if (angular.element(element).attr('type') === 'radio' && $attrs['ngModel']) {
              return $scope.$apply(function () {
                //set up for radio buttons to be de-selectable
                if (ngModel.$viewValue != value) {
                  return ngModel.$setViewValue(value);
                }
                else {
                  ngModel.$setViewValue(null);
                }
                ngModel.$render();
                return;
              });
            }
          });
        });
      }
    };
  });
})();
