(function() {
  'use strict';

  angular.module('auth.services')
  .factory('auth_service', function($http, $q, $window, permissions_management, session, ApiHost, socket) {
    
    var user_name = '';
    var user_surname = '';
    var user_profile = '';
    var user_profile_id = '';
    var isAuthenticated = false;
    var role = '';
    var authToken;
    var user_id = '';
    
    function loadUserCredentials() {
      var profile = session.recover();
      
      if (profile) {
        useCredentials(profile);
      }
    }
    
    function storeUserCredentials(profile) {
      session.store(profile);
      useCredentials(profile);
    }
    
    function useCredentials(profile) {
      
      user_id = profile._id;
      user_name = profile.name;
      user_surname = profile.surname;
      user_profile = '';
      user_profile_id = '';
      
      if (profile.profile) {
        socket.emit('user-logged-in', profile.profile._id);
        user_profile_id = profile.profile._id;
        user_profile = profile.profile.name;
      }
      
      isAuthenticated = true;
      authToken = profile.token;
      
      //if (user_name == 'admin') {
        //role = USER_ROLES.admin
      //}
      //if (user_name == 'user') {
        //role = USER_ROLES.public
      //}
      
      // Set the token as header for your requests!
      $http.defaults.headers.common['Authorization'] = 'Bearer ' + authToken;
      $http.defaults.headers.common['Content-Type'] = 'application/json';
    }
    
    function destroyUserCredentials() {
      authToken = undefined;
      user_name = '';
      user_surname = '';
      user_profile = '';
      user_profile_id = '';
      user_id = '';
      isAuthenticated = false;
      $http.defaults.headers.common['Authorization'] = undefined;
      session.remove();
    }
    
    var admin_login = function(credentials) {
      return $q(function(resolve, reject) {

        //if ((credentials.email == 'admin@test.com' && credentials.password == 'test123') || (credentials.email == 'user' && credentials.password == 'test123')) {
        //  // Make a request and receive your auth token from your server
        //  var current_user = {user_id:1, name: 'Angel', surname: 'Dimaria', token: 'DAdsXadecS546asdae'};
        //  current_user.permissions = {users: {find: ['all'], create: ['users'], update:[], remove:[]}};
        //  
        //  storeUserCredentials(current_user);
        //  resolve('Login success.');
        //} else {
        //  reject('Credenciales incorrectas.');
        //}
        //return;
        
        var login = $http.post(ApiHost + 'admin_login', credentials);
        login.success(function(result) {
          
          if (result.error) {
            reject(result.error);
          }
          else {
            
            var current_user = result.response.user;
            current_user.token = result.response.token;
            
            //var permissions = [
            //  {users: {permission_names: ['users_can_find', 'users_can_update', 'users_can_view_report'], remove: [], update: [], create: [], find: ['all']}},
            //  {profiles: {permission_names: ['profiles_can_find'], remove: [], update: [], create: [], find: ['all']}}
            //];
            
            //var permissions = permissions_management.build_permissions(result.response.user.profile.permissions);
            //current_user.permissions = permissions;
            
            storeUserCredentials(current_user);
            resolve(result.response);
          }
        });
        
        login.error(function(error) {
          reject(error);
        });
      });
    };
    
    var logout = function() {
      destroyUserCredentials();
    };
    
    var isAuthorized = function(authorizedRoles) {
      if (!angular.isArray(authorizedRoles)) {
        authorizedRoles = [authorizedRoles];
      }
      return (isAuthenticated && authorizedRoles.indexOf(role) !== -1);
    };
    
    var register = function(data) {

      destroyUserCredentials();
      
      return $q(function(resolve, reject) {
        
        var register = $http.post(ApiHost + 'register', data);
        
        register.success(function(result) {
          if (result.response.error) {
            reject(result.response.message);
          }
          else{
            storeUserCredentials(result.response);
            resolve('Usuario registrado.');
          }
        });
      });
    }
    
    loadUserCredentials();
   
    return {
      admin_login: admin_login,
      logout: logout,
      isAuthorized: isAuthorized,
      isAuthenticated: function() {return isAuthenticated;},
      user_name: function() {return user_name;},
      user_surname: function() {return user_surname;},
      user_profile: function() {return user_profile;},
      user_profile_id: function() {return user_profile_id;},
      user_id: function() {return user_id;},
      authToken: function() {return authToken;},
      role: function() {return role;},
      register: register
    };
  });
})();
(function() {
  'use strict';
  
  angular.module('core.directives')
  .directive('authorizeSection', function($parse, session, _) {
    
    return {
      restrict: 'A',
      scope: {
        authorizeSection: '='
      },
      transclude:'element',
      priority: 900,
      link: function(scope, element, attributes, nullController, transclude) {
        var permissions = session.permissions();
        
        var authorize_show = scope.authorizeSection;
        if (!_(authorize_show).isArray()) {
          authorize_show = [authorize_show];
        }
        
        var has_permission = _(authorize_show).reduce(function(memo, string) {
          var negated_permission = (string.indexOf('!') === 0);
          if (negated_permission) { string = string.replace('!', ''); }
          
          var string_has_permission = determine_string_permissions(string);
          
          if (negated_permission) { string_has_permission = !string_has_permission; }
          
          return memo || string_has_permission;
          
        }, false);
        
        var childScope = scope.$parent.$new();
        transclude(childScope, function(clone) {
          
          if (!has_permission) {
            angular.element(clone).remove();
            angular.element(element).remove();
            childScope.$destroy();
            return;
          }
          
          element.after(clone);
          
          // TO PREVENT MEMORY LEAKS
          scope.$on('$destroy', function() {
            angular.element(clone).remove();
            angular.element(element).remove();
            childScope.$destroy();
          });
          // TO PREVENT MEMORY LEAKS
  
        });
        
        function determine_string_permissions(string) {
          var split = string.split('.');
          var actions = [split[0], split[1]];
          
          if (split.length > 2) {
            var rest = _(split).chain().rest().rest().value();
            actions.push(rest.join('.'));
          }
  
          if (actions.length === 3) {
            var module = _(actions).initial().join('.');
            var module_permissions = $parse(module)(permissions);
            
            var has_permission = _(module_permissions).contains(actions[2]) || _(module_permissions).contains('all');
            
            if(!has_permission && module_permissions && _(module_permissions[0]).isString()){
              return _(module_permissions).contains(actions[2]);
            }
            
            return has_permission;
          }
          
          if (string.match('current_action')) {
            string = string.replace('current_action', scope.$parent.current_action);
          }
          
          return Boolean($parse(string)(permissions).length);
        }
      }
    };
  });
})();
(function() {
  'use strict';
  
  angular.module('core.directives')
  .directive('authorizeFields', function($parse, session, _) {
  
    function remove_unauthorized_fields(fields, paths){
      _(fields).each(function(field) {
        var path = angular.element(field).attr('ng-model') || angular.element(field).attr('ng-bind');
        var base_property_path = _(path.split('.')).first(2).join('.');
  
        if(!_(paths).contains(base_property_path)){
          angular.element(field).parents('.form-group, .field-container, .profile-info-row').first().remove();
        }
      });
    }
  
    return {
      restrict: 'A',
      scope: {
        authorizeFields: '@'
      },
      link: function(scope, element) {
        var permissions = session.permissions();
        var params = scope.authorizeFields.split(/\sin\s|\son\s/);
        var module = params[1];
        var model = params[0];
        var action = params[2];
  
        var authorized_paths = $parse(module + '.' + action)(permissions);
        var original_action_has_all = _($parse(module + '.' + action)(permissions)).contains('all');
        var can_see_paths = $parse(module + '.find')(permissions);
  
        can_see_paths = _(can_see_paths).difference(authorized_paths);
  
        authorized_paths = _(authorized_paths).union(can_see_paths);
  
        if(_(authorized_paths).contains('all')){
  
          authorized_paths = _(authorized_paths).map(function(path) { return model+'.'+path; });
          var all_fields = angular.element(element).find('[ng-model^='+model+']');
  
          _(all_fields).each(function(field) {
            var path = angular.element(field).attr('ng-model') || angular.element(field).attr('ng-bind');
            var base_property_path = _(path.split('.')).first(2).join('.');
  
            if(!_(authorized_paths).contains(base_property_path) && !original_action_has_all){
              angular.element(field).attr('disabled', 'disabled');
            }
          });
  
          return true;
        }
  
        authorized_paths = _(authorized_paths).map(function(path) { return model+'.'+path; });
        can_see_paths = _(can_see_paths).map(function(path) { return model+'.'+path; });
  
        var fields = angular.element(element).find('[ng-model^='+model+'], [ng-bind^='+model+']');
        remove_unauthorized_fields(fields, authorized_paths);
  
        _(can_see_paths).each(function(path) {
          var field = angular.element(element).find('[ng-model^="'+path+'"]');
          field.attr('disabled', 'disabled');
        });
  
      }
    };
  });
})();

(function() {
  'use strict';
  
  angular.module('core.directives')
  .directive('accessSection', function($parse, session, _) {
    
    return {
      restrict: 'A',
      scope: {
        accessSection: '='
      },
      transclude:'element',
      priority: 900,
      link: function(scope, element, attributes, nullController, transclude) {
        var permissions = session.permissions();
        
        var authorize_show = scope.accessSection;
        var has_permission = determine_string_permissions(authorize_show);
        
        var childScope = scope.$parent.$new();
        transclude(childScope, function(clone) {
          
          if(!has_permission){
            angular.element(clone).remove();
            angular.element(element).remove();
            childScope.$destroy();
            return;
          }
          
          element.after(clone);
          
          // TO PREVENT MEMORY LEAKS
          scope.$on('$destroy', function() {
            angular.element(clone).remove();
            angular.element(element).remove();
            childScope.$destroy();
          });
          // TO PREVENT MEMORY LEAKS
  
        });
        
        function determine_string_permissions(action) {
          return _(permissions.permissions_names).contains(action);
        }
      }
    };
  });
})();
(function() {
  'use strict';
  
  angular.module('admin.advertisements', []);
})();
(function() {
  'use strict';
  
  angular.module('admin.advertisements')
  .controller('advertisementUpdateController', advertisementUpdateController)
  .config(function($stateProvider) {
    $stateProvider
      .state('admin.advertisements.update', {
        url: '/update/:_id',
        title: 'Editar Publicidad',
        templateUrl: 'app/modules/advertisements/views/advertisements.update.html',
        controller: 'advertisementUpdateController',
        controllerAs: 'advertisementCtrl',
        resolve: {          
          advertisement: function(advertisements_model, $stateParams) {
            return advertisements_model.get($stateParams._id);
          },
          initial_data: function(advertisements_model, Catalog) {
            return advertisements_model.initial_data({code_advertisements_type: Catalog.advertisements_type});
          }          
        }
      });
  });
  
  function advertisementUpdateController($state, advertisements_model, Notifier, initial_data, advertisement, _) {
    var vm = this;
    
    vm.types = initial_data.types;
    vm.advertisement = advertisement;
    
    view_types();
    
    //verify types selected
    function view_types() {
      _.each(initial_data.advertisements, function(advertisement) {
        var type_selected = _.findWhere(vm.types, {_id: advertisement.type});
        if (advertisement.type !== vm.advertisement.type) {
          vm.types = _.without(vm.types, type_selected); 
        }              
      });
    }    

    vm.update = function() {
      if (vm.advertisement.text_html.trim()==="") {
        Notifier.warning({custom_message: 'Se debe ingresar un texto'});
      }else{
        advertisements_model.update(vm.advertisement).then(function() {
          Notifier.success({custom_message: 'Publicidad Actualizada.'});
          $state.go('admin.advertisements.list');
        });        
      }
    };    

  }
})();
(function() {
  'use strict';
  
  angular.module('admin.advertisements')
  .controller('advertisementListController', advertisementListController)
  .config(function($stateProvider) {
    $stateProvider
      .state('admin.advertisements.list', {
        url: '/list',
        title: 'Lista de Publicidad',
        templateUrl: 'app/modules/advertisements/views/advertisements.list.html',
        controller: 'advertisementListController',
        controllerAs: 'advertisementCtrl',
        resolve: {
          advertisements: function(advertisements_model) {
            return advertisements_model.get();
          }
        }
      });
  });
  
  function advertisementListController($state, advertisements_model, advertisements, NgTableParams) {
    var vm = this;    
    
    vm.advertisements = advertisements;
    
    //for crud
    vm.remove_advertisement = function(advertisement) {
      vm.close_remove_advertisement_modal = false;
      vm.advertisement = advertisement;
    };
    
    vm.delete_advertisement = function() {      
      vm.close_remove_advertisement_modal = true;
      
      advertisements_model.remove(vm.advertisement).then(function() {
        var index = vm.advertisements.indexOf(vm.advertisement);
        vm.advertisements.splice(index,1);    
        set_advertisements_pagination();
      });              
    };     
    
    vm.edit_advertisement = function(advertisement) {
      $state.go('admin.advertisements.update', { _id: advertisement._id });
    };
    
    //pagination    
    set_advertisements_pagination();
    
    function set_advertisements_pagination() {
      vm.advertisementsTableParams = new NgTableParams({
      }, {        
        dataset: vm.advertisements
      });      
    }
    
  }
})();
(function() {
  'use strict';
  
  angular
  .module('admin.advertisements')
  .controller('advertisementsCreateController', advertisementsCreateController)
  .config(function($stateProvider) {
    $stateProvider
      .state('admin.advertisements.create', {
        url: '/create',
        title: 'Nueva Marca',
        templateUrl: 'app/modules/advertisements/views/advertisements.create.html',
        controller: 'advertisementsCreateController',
        controllerAs: 'advertisementCtrl',
        resolve: {
          initial_data: function(advertisements_model, Catalog) {
            return advertisements_model.initial_data({code_advertisements_type: Catalog.advertisements_type});
          }
        }         
      });
  });
  
  function advertisementsCreateController($rootScope, $state, advertisements_model, initial_data, Notifier, _) {
    var vm = this;
    
    vm.types = initial_data.types;
    
    view_types();
    
    //verify types selected
    function view_types() {      
      _.each(initial_data.advertisements, function(advertisement) {
        var type_selected = _.findWhere(vm.types, {_id: advertisement.type});
        vm.types = _.without(vm.types, type_selected);         
      });
    }
         
    vm.save = function() {
      if (vm.advertisement.text_html.trim()==="") {
        Notifier.warning({custom_message: 'Se debe ingresar un texto'});
      }else{
        advertisements_model.create(vm.advertisement).then(function() {
          Notifier.success({custom_message: 'Publicidad creada.'});
          $state.go('admin.advertisements.list');
        });        
      }

    };      
 
  }  
})();
